let CONFIG = {}; //! –ø–∞—Ä–∞–º–µ—Ç—Ä—ã –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏–∏ –≤–∞–ª–∏–¥–∞—Ü–∏–∏

//! –ö–æ–Ω—Ç—Ä–æ–ª—å –¥–æ—Å–ø–Ω–æ—Å—Ç–∏ submit-–∫–Ω–æ–ø–∫–∏ (disable=true - –∫–Ω–æ–ø–∫–∞ –±–ª–æ–∫–∏—Ä–æ–≤–∞–Ω–∞)
const enableButton = (button, disable) => {
  button.disabled = disable;
  if (disable) button.classList.add(CONFIG.inactiveButtonClass);
  else button.classList.remove(CONFIG.inactiveButtonClass);
};

//! –û–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –≤–∞–ª–∏–¥–Ω–æ—Å—Ç–∏ –ø–æ–ª–µ–π –≤–≤–æ–¥–∞
const hasInvalidInput = (inputList) => {
  return inputList.some((inputElement) => {
    return !inputElement.validity.valid;
  });
};

//! –£–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç—å—é submit-–∫–Ω–æ–ø–∫–∏ –ø–æ —Å–æ—Å—Ç–æ—è–Ω–∏—é –ø–æ–ª–µ–π –≤–≤–æ–¥–∞:
//! –µ—Å–ª–∏ —Ö–æ—Ç—è –±—ã –æ–¥–∏–Ω input –Ω–µ–≤–∞–ª–∏–¥–Ω—ã–π, —Ç–æ submit-–∫–Ω–æ–ø–∫–∞ –Ω–µ –¥–æ—Å—Ç—É–ø–Ω–∞
const toggleButtonState = (inputList, button) => {
  enableButton(button, hasInvalidInput(inputList));
};

//!
const showInputError = (form, input, errorMessage) => {
  const errorElement = form.querySelector(`.${input.id}-error`);
  input.classList.add(CONFIG.inputErrorClass);
  errorElement.classList.add(CONFIG.errorClass);
  errorElement.textContent = errorMessage;
};

//!
const hideInputError = (form, input) => {
  const errorElement = form.querySelector(`.${input.id}-error`);
  input.classList.remove(CONFIG.inputErrorClass);
  errorElement.classList.remove(CONFIG.errorClass);
  errorElement.textContent = "";
};

//! –ö–æ–Ω—Ç—Ä–æ–ª—å –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –∫–∞—Å—Ç–æ–º–Ω–æ–≥–æ —Å–æ–æ–±—â–µ–Ω–∏—è –æ–± –æ—à–∏–±–∫–µ –≤–≤–æ–¥–∞
const isValid = (form, input) => {
  if (input.validity.patternMismatch)
    input.setCustomValidity(input.dataset.errorMessage);
  else input.setCustomValidity("");

  //* –£–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ–º —Å–æ–æ–±—â–µ–Ω–∏—è –æ–± –æ—à–∏–±–∫–µ –≤–≤–æ–¥–∞ –≤ input
  if (!input.validity.valid)
    showInputError(form, input, input.validationMessage);
  else hideInputError(form, input);
};

//! –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ –≤—Å–µ–º input –≤—ã–±—Ä–∞–Ω–Ω–æ–π —Ñ–æ—Ä–º—ã
const setEventListeners = (form) => {
  const inputList = Array.from(form.querySelectorAll(CONFIG.inputSelector));
  const button = form.querySelector(CONFIG.submitButtonSelector);

  // –°–æ—Å—Ç–æ—è–Ω–∏–µ –∫–Ω–æ–ø–∫–∏ –ø–µ—Ä–µ–¥ –≤–≤–æ–¥–æ–º –¥–∞–Ω–Ω—ã—Ö –≤ input
  toggleButtonState(inputList, button);

  // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ –≤—Å–µ–º –ø–æ–ª—è–º –≤–≤–æ–¥–∞ —Ñ–æ—Ä–º—ã
  inputList.forEach((input) => {
    input.addEventListener("input", () => {
      //* –ö–æ–Ω—Ç—Ä–æ–ª—å –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –∫–∞—Å—Ç–æ–º–Ω–æ–≥–æ —Å–æ–æ–±—â–µ–Ω–∏—è –æ–± –æ—à–∏–±–∫–µ –≤–≤–æ–¥–∞
      isValid(form, input);
      toggleButtonState(inputList, button);
    });
  });
  // }
};

//! –í–∫–ª—é—á–µ–Ω–∏–µ –≤–∞–ª–∏–¥–∞—Ü–∏–∏ (–æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ —Å–æ–±—ã—Ç–∏–π)
function enableValidation({
  formSelector,
  inputSelector,
  submitButtonSelector,
  inactiveButtonClass,
  inputErrorClass,
  errorClass,
} = {}) {
  CONFIG = arguments[0]; // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤ –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏–∏ –º–æ–¥—É–ª—è

  // –°–æ–∑–¥–∞–Ω–∏–µ —Å–ø–∏—Å–∫–∞ —Ñ–æ—Ä–º —Å input-–ø–æ–ª—è–º–∏
  const formsList = Array.from(
    document.querySelectorAll(
      `${CONFIG.formSelector}:has(${CONFIG.inputSelector})`
    )
  );
  // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ –ø–æ–ª—è–º –≤—ã–±—Ä–∞–Ω–Ω—ã—Ö —Ñ–æ—Ä–º
  formsList.forEach((form) => {
    setEventListeners(form);
  });
}

//! –û—á–∏—Å—Ç–∫–∞ –æ—à–∏–±–æ–∫ –≤–∞–ª–∏–¥–∞—Ü–∏–∏ —Ñ–æ—Ä–º—ã –∏ –¥–µ–∞–∫—Ç–∏–≤–∞—Ü–∏—è –∫–Ω–æ–ø–∫–∏ (–ø—Ä–∏ –Ω–µ—É–¥–∞—á–Ω–æ–π –≤–∞–ª–∏–¥–∞—Ü–∏–∏)
function clearValidation(form) {
  // –û—á–∏—Å—Ç–∫–∞ –æ—à–∏–±–æ–∫ –≤–≤–æ–¥–∞ –¥–∞–Ω–Ω—ã—Ö –≤ –ø–æ–ª—è —Ñ–æ—Ä–º—ã
  const inputList = Array.from(form.querySelectorAll(CONFIG.inputSelector));
  inputList.forEach((input) => hideInputError(form, input));

  // –î–µ–∞–∫—Ç–∏–≤–∞—Ü–∏—è submit-–∫–Ω–æ–ø–∫–∏
  const button = form.querySelector(CONFIG.submitButtonSelector);
  enableButton(button, true);
}

export { enableValidation, clearValidation };
