export class Profile {
  constructor(data = {}) {
    this.data = data; // –ò—Å–ø–æ–ª—å–∑—É–µ–º —Å–µ—Ç—Ç–µ—Ä –ø—Ä–∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏
  }

  set data({ name, about, avatar, _id, cohort } = {}) {
    Object.assign(this, { name, about, avatar, _id, cohort });
  }

  get data() {
    return {
      name: this.name,
      about: this.about,
      avatar: this.avatar,
      _id: this._id,
      cohort: this.cohort,
    };
  }
}
