// import initialCards from "../components/cards.js";
import { Profile } from "../components/profile.js";
import { createCard, removeCard } from "../components/card.js";
import { openModal, closeModal, initializeModal } from "../components/modal.js";
import { enableValidation, clearValidation } from "../components/validation.js";
import "../pages/index.css";

import {
  getCards,
  getProfile,
  setProfile,
  setProfileAvatar,
  getHeaders,
  saveCard,
  deleteCard,
  evaluateCard,
} from "../components/api.js";

//! –ü—Ä–æ—Ñ–∏–ª—å
const profile = new Profile();

//! –°–µ–ª–µ–∫—Ç–æ—Ä—ã
const SELECTORS = {
  formSelector: ".popup__form",
  inputSelector: ".popup__input",
  submitButtonSelector: ".popup__button",
  inactiveButtonClass: "popup__button_inactive",
  inputErrorClass: "popup__input_type_error",
  errorClass: "popup__error_visible",
};

//!  DOM —É–∑–ª—ã
const cardsContainer = document.querySelector(".places__list"); // –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –∫–∞—Ä—Ç–æ—á–µ–∫
//* popup's
const profilePopup = document.querySelector(".popup.popup_type_edit"); // —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –ø—Ä–æ—Ñ–∏–ª—è
const newCardPopup = document.querySelector(".popup.popup_type_new-card"); // —Å–æ–∑–¥–∞–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
const cardViewPopup = document.querySelector(".popup.popup_type_image"); // –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ –∫–∞—Ä—Ç–æ—á–∫–∏
const avatarPopup = document.querySelector(".popup_type_avatar"); // –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ –∫–∞—Ä—Ç–æ—á–∫–∏
const cardDelConfirmPopup = document.querySelector(".popup_confirm-del-card"); // –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ –∫–∞—Ä—Ç–æ—á–∫–∏

//* –ö–Ω–æ–ø–∫–∏ –æ—Ç–∫—Ä—ã—Ç–∏—è popup
const btnEditProfile = document.querySelector(".profile__edit-button"); // —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –ø—Ä–æ—Ñ–∏–ª—è
const btnAddCard = document.querySelector(".profile__add-button"); // –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –Ω–æ–≤–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏

//* –ò—Å—Ç–æ—á–Ω–∏–∫–∏ –¥–∞–Ω–Ω—ã—Ö –¥–ª—è —Ñ–æ—Ä–º popup
const nameProfile = document.querySelector(".profile__title"); // –∏–º—è –ø—Ä–æ—Ñ–∏–ª—è
const descrProfile = document.querySelector(".profile__description"); // –æ–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ—Ñ–∏–ª—è
const avatarProfile = document.querySelector(".profile__image"); // –æ–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ—Ñ–∏–ª—è

//* –≠–ª–µ–º–µ–Ω—Ç—ã popup –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ –∫–∞—Ä—Ç–æ—á–∫–∏
const imageCardViewPopup = cardViewPopup.querySelector("img.popup__image");
const captionCardViewPopup = cardViewPopup.querySelector(".popup__caption");

//* Inputs —Ñ–æ—Ä–º—ã popup —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –ø—Ä–æ—Ñ–∏–ª—è
const nameProfilePopupInput = profilePopup.querySelector(
  "input.popup__input_type_name"
); // input –∏–º–µ–Ω–∏ –ø—Ä–æ—Ñ–∏–ª—è
const descrProfilePopupInput = profilePopup.querySelector(
  "input.popup__input_type_description"
); // input –æ–ø–∏—Å–∞–Ω–∏—è –ø—Ä–æ—Ñ–∏–ª—è

//* Input —Ñ–æ—Ä–º—ã popup –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –∞–≤–∞—Ç–∞—Ä
const urlAvatarPopupInput = avatarPopup.querySelector(
  "input.popup__input_type_avatar"
);

//! –û–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ —Å–æ–±—ã—Ç–∏–π

//* –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è popup –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ –∫–∞—Ä—Ç–æ—á–∫–∏
function handleShowCard(card) {
  setupCardViewPopup(card); // –Ω–∞—Å—Ç—Ä–æ–π–∫–∞ popup
  if (cardViewPopup) openModal(cardViewPopup); // –æ—Ç–∫—Ä—ã—Ç–∏–µ popup
}

//! –ù–∞–∑–Ω–∞—á–µ–Ω–∏–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ —Å–æ–±—ã—Ç–∏–π ...

(() => {
  //* ... –∫–Ω–æ–ø–∫–∏ –æ—Ç–∫—Ä—ã—Ç–∏—è popup —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –ø—Ä–æ—Ñ–∏–ª—è
  if (btnEditProfile) {
    btnEditProfile.addEventListener("click", () => {
      if (profilePopup) {
        setupEditProfilePopup(); // –Ω–∞—Å—Ç—Ä–æ–π–∫–∞ popup
        openModal(profilePopup); // –æ—Ç–∫—Ä—ã—Ç–∏–µ popup

        // –°–±—Ä–æ—Å –æ—à–∏–±–æ–∫ –≤–≤–æ–¥–∞ –∏ –¥–µ–∞–∫—Ç–∏–≤–∞—Ü–∏—è submit-–∫–Ω–æ–ø–∫–∏
        const formEditProfile = profilePopup.querySelector(".popup__form");
        clearValidation(formEditProfile);
      }
    });
  }

  //* ... –∫–Ω–æ–ø–∫–∏ –æ—Ç–∫—Ä—ã—Ç–∏—è popup —Å–æ–∑–¥–∞–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
  if (btnAddCard) {
    // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∞ –æ—Ç–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –∏ —Å–±—Ä–æ—Å —Ñ–æ—Ä–º—ã
    btnAddCard.addEventListener("click", () => {
      if (newCardPopup) {
        resetNewCardPopupForm(); // —Å–±—Ä–æ—Å —Ñ–æ—Ä–º—ã
        openModal(newCardPopup); // –æ—Ç–∫—Ä—ã—Ç–∏–µ popup
        // –°–±—Ä–æ—Å –æ—à–∏–±–æ–∫ –≤–≤–æ–¥–∞ –∏ –¥–µ–∞–∫—Ç–∏–≤–∞—Ü–∏—è submit-–∫–Ω–æ–ø–∫–∏
        const formCardProfile = newCardPopup.querySelector(".popup__form");
        clearValidation(formCardProfile);
      }
    });
  }

  //* ... –∫–Ω–æ–ø–∫–∏ –æ—Ç–∫—Ä—ã—Ç–∏—è popup –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –∞–≤–∞—Ç–∞—Ä
  if (avatarProfile) {
    // –û–±—Ä–∞–±–æ—Ç–∫–∞ –Ω–∞–≤–µ–¥–µ–Ω–∏—è –∫—É—Ä—Å–æ—Ä–∞ –Ω–∞ –∞–≤–∞—Ç–∞—Ä–∫—É
    avatarProfile.addEventListener("mouseenter", () => {
      avatarProfile.classList.add("avatar-edit_active");
    });

    // –û–±—Ä–∞–±–æ—Ç–∫–∞ —É—Ö–æ–¥–∞ –∫—É—Ä—Å–æ—Ä–∞ –Ω–∞ –∞–≤–∞—Ç–∞—Ä–∫—É
    avatarProfile.addEventListener("mouseleave", () => {
      avatarProfile.classList.remove("avatar-edit_active");
    });

    // –û–±—Ä–∞–±–æ—Ç–∫–∞ –∫–ª–∏–∫–∞ –ø–æ –∞–≤–∞—Ç–∞—Ä–∫–µ
    avatarProfile.addEventListener("click", () => {
      if (avatarProfile.classList.contains("avatar-edit_active")) {
        if (avatarPopup) {
          setupAvatarPopup(); // –Ω–∞—Å—Ç—Ä–æ–π–∫–∞ popup
          openModal(avatarPopup); // –æ—Ç–∫—Ä—ã—Ç–∏–µ popup
          // –°–±—Ä–æ—Å –æ—à–∏–±–æ–∫ –≤–≤–æ–¥–∞ –∏ –¥–µ–∞–∫—Ç–∏–≤–∞—Ü–∏—è submit-–∫–Ω–æ–ø–∫–∏
          const formEditAvatar = avatarPopup.querySelector(".popup__form");
          clearValidation(formEditAvatar);
        }
      }
    });
  }

  //* ... –∑–∞–∫—Ä—ã—Ç–∏—è popup –∏ –µ–≥–æ –ø—Ä–µ–¥–Ω–∞—Å—Ç—Ä–æ–π–∫–∞
  const popups = document.querySelectorAll(".popup");
  popups.forEach((popup) => {
    initializeModal(popup);
  });

  //* ... submit-–æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∞ —Ñ–æ—Ä–º–∞–º popup
  setHandlerFormSubmit("edit-profile", handleEditProfileSubmit);
  setHandlerFormSubmit("new-place", handleNewCardSubmit);
  setHandlerFormSubmit("edit-avatar", handleAvatarSubmit);
  setHandlerFormSubmit("confirm-del-card", handleCardDelSubmit);
})();

function setHandlerFormSubmit(formName, handler) {
  if (document.forms[formName]) {
    document.forms[formName].addEventListener("submit", handler);
  }
}

function handleEditProfileSubmit(evt) {
  evt.preventDefault(); // –±–ª–æ–∫–∏—Ä–æ–≤–∫–∞ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–π –æ–±—Ä–∞–±–æ—Ç–∫–∏ —Ñ–æ—Ä–º—ã
  toggleSubmitButtonText(profilePopup); // toggle —Ç–µ–∫—Å—Ç–∞ submit-–∫–Ω–æ–ø–∫–∏ ("–°—Ö—Ä–∞–Ω–µ–Ω–∏–µ...")
  const data = serializeForm(evt.target); // –ø–æ–¥–≥–æ—Ç–æ–≤–∫–∞ –¥–∞–Ω–Ω—ã—Ö —Ñ–æ—Ä–º—ã
  // –î–∞–Ω–Ω—ã–µ –∏–∑ —Ñ–æ—Ä–º—ã –ø–æ–ª—É—á–µ–Ω—ã (–æ–±—ä–µ–∫—Ç –¥–ª—è body –∑–∞–ø—Ä–æ—Å–∞ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω)?
  if (data) {
    // –û—Ç–ø—Ä–∞–≤–∫–∞ –¥–∞–Ω–Ω—ã—Ö –Ω–∞ —Å–µ—Ä–≤–µ—Ä
    setProfile(data)
      .then((profileData) => {
        renderProfile(profileData);
        closeModal(profilePopup);
      })
      .catch((err) => {
        console.log(err); // –≤—ã–≤–æ–¥ –æ—à–∏–±–∫b –≤ –∫–æ–Ω—Å–æ–ª—å
      })
      .finally(() => {
        toggleSubmitButtonText(profilePopup); // toggle —Ç–µ–∫—Å—Ç–∞ submit-–∫–Ω–æ–ø–∫–∏ ("–°—Ö—Ä–∞–Ω–∏—Ç—å")
      });
  } else closeModal(profilePopup);
}

function handleNewCardSubmit(evt) {
  evt.preventDefault(); // –±–ª–æ–∫–∏—Ä–æ–≤–∫–∞ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–π –æ–±—Ä–∞–±–æ—Ç–∫–∏ —Ñ–æ—Ä–º—ã
  toggleSubmitButtonText(newCardPopup); // toggle —Ç–µ–∫—Å—Ç–∞ submit-–∫–Ω–æ–ø–∫–∏ ("–°—Ö—Ä–∞–Ω–µ–Ω–∏–µ...")
  const data = serializeForm(evt.target); // –ø–æ–¥–≥–æ—Ç–æ–≤–∫–∞ –¥–∞–Ω–Ω—ã—Ö —Ñ–æ—Ä–º—ã
  // –î–∞–Ω–Ω—ã–µ –∏–∑ —Ñ–æ—Ä–º—ã –ø–æ–ª—É—á–µ–Ω—ã (–æ–±—ä–µ–∫—Ç –¥–ª—è body –∑–∞–ø—Ä–æ—Å–∞ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω)?
  if (data) {
    // –û—Ç–ø—Ä–∞–≤–∫–∞ –¥–∞–Ω–Ω—ã—Ö –Ω–∞ —Å–µ—Ä–≤–µ—Ä
    saveCard({ name: data["place-name"], link: data.link })
      .then((respData) => {
        if (respData) {
          // –°–æ–∑–¥–∞–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏
          const newCard = createCard(respData, profile._id, {
            onShow: handleShowCard,
            onDelete: handleDeleteCard,
            onLike: handleLikeCard,
          });
          // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ —Å–æ–∑–¥–∞–Ω–Ω–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏ –≤ –Ω–∞—á–∞–ª–æ —Å–ø–∏—Å–∫–∞
          if (newCard && cardsContainer) cardsContainer.prepend(newCard);
          closeModal(newCardPopup);
        }
      })
      .catch((err) => {
        console.log("–û—à–∏–±–∫–∞ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏:", err); // –≤—ã–≤–æ–¥ –æ—à–∏–±–∫b –≤ –∫–æ–Ω—Å–æ–ª—å
      })
      .finally(() => {
        toggleSubmitButtonText(newCardPopup); // toggle —Ç–µ–∫—Å—Ç–∞ submit-–∫–Ω–æ–ø–∫–∏ ("–°—Ö—Ä–∞–Ω–∏—Ç—å")
      });
  } else closeModal(newCardPopup);
}

function handleAvatarSubmit(evt) {
  evt.preventDefault(); // –±–ª–æ–∫–∏—Ä–æ–≤–∫–∞ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–π –æ–±—Ä–∞–±–æ—Ç–∫–∏ —Ñ–æ—Ä–º—ã
  toggleSubmitButtonText(avatarPopup); // toggle —Ç–µ–∫—Å—Ç–∞ submit-–∫–Ω–æ–ø–∫–∏ ("–°—Ö—Ä–∞–Ω–µ–Ω–∏–µ...")
  const data = serializeForm(evt.target); // –ø–æ–¥–≥–æ—Ç–æ–≤–∫–∞ –¥–∞–Ω–Ω—ã—Ö —Ñ–æ—Ä–º—ã

  // –î–∞–Ω–Ω—ã–µ –∏–∑ —Ñ–æ—Ä–º—ã –ø–æ–ª—É—á–µ–Ω—ã (–æ–±—ä–µ–∫—Ç –¥–ª—è body –∑–∞–ø—Ä–æ—Å–∞ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω)?
  if (data && data.link) {
    const avatar = data.link;
    // –ü—Ä–æ–≤–µ—Ä–∫–∞: MIME-—Ç–∏–ø —Å—Å—ã–ª–∫–∏ - image?
    if (avatar) {
      getHeaders(avatar)
        .then((resp) => {
          if (!resp.ok) return Promise.reject(resp.status);
          // –ü—Ä–æ–≤–µ—Ä–∫–∞: —Ç–∏–ø –∫–æ–Ω—Ç–µ–Ω—Ç–∞ - –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ?
          const contentType = resp.headers.get("Content-Type"); // —Ç–∏–ø –∫–æ–Ω—Ç–µ–Ω—Ç–∞ –≤ –æ—Ç–≤–µ—Ç–µ
          if (!contentType || !contentType.startsWith("image/"))
            return Promise.reject(`—Å—Å—ã–ª–∫–∞ –Ω–µ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É`);
          // –°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ fetch-–∑–∞–ø—Ä–æ—Å–æ–º —Å—Å—ã–ª–∫–∏ –Ω–∞ –Ω–æ–≤—ã–π –∞–≤–∞—Ç–∞—Ä
          return setProfileAvatar({ avatar });
        })
        .then((avatarData) => {
          renderProfile(avatarData); // –≤—ã–≤–æ–¥ –¥–∞–Ω–Ω—ã—Ö –ø—Ä–æ—Ñ–∏–ª—è –∏ –µ–≥–æ –∞–≤–∞—Ç–∞—Ä –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É
          closeModal(avatarPopup);
        })
        .catch((err) => {
          console.log("–û—à–∏–±–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –∞–≤–∞—Ç–∞—Ä:", err);
        })
        .finally(() => {
          toggleSubmitButtonText(avatarPopup); // toggle —Ç–µ–∫—Å—Ç–∞ submit-–∫–Ω–æ–ø–∫–∏ ("–°—Ö—Ä–∞–Ω–∏—Ç—å")
        });
    } else closeModal(avatarPopup);
  } else closeModal(avatarPopup);
}

function handleCardDelSubmit(evt) {
  evt.preventDefault(); // –±–ª–æ–∫–∏—Ä–æ–≤–∫–∞ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–π –æ–±—Ä–∞–±–æ—Ç–∫–∏ —Ñ–æ—Ä–º—ã
  if ("cardId" in evt.target.dataset) {
    const cardId = evt.target.dataset.cardId;
    deleteCard(cardId)
      .then((resp) => {
        if (!resp.ok) return Promise.reject(resp.status);
        closeModal(cardDelConfirmPopup);
        removeCard(cardId); // —É–¥–∞–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏ —Å–æ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
      })
      .catch((err) => {
        console.log("–û—à–∏–±–∫–∞ —É–¥–∞–ª–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏:", err);
      });
    // –£–¥–∞–ª–µ–Ω–∏–µ –∞—Ç—Ç—Ä–∏–±—É—Ç–∞, —Ö—Ä–∞–Ω—è—â–µ–≥–æ Id –∫–∞—Ä—Ç–æ—á–∫–∏
    delete evt.target.dataset.cardId;
  } else closeModal(cardDelConfirmPopup);
}

//! –í—Å–ø–æ–º–æ–≥–∞—Ç–µ–ª—å–Ω—ã–µ —Ñ—É–Ω–∫—Ü–∏–∏ popup –∏ –µ–≥–æ —Ñ–æ—Ä–º

//* –ò–∑–º–µ–Ω–µ–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ submit-–∫–Ω–æ–ø–∫–∏ popup –≤ –ø—Ä–æ—Ü–µ—Å—Å–µ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è
function toggleSubmitButtonText(popup) {
  if (popup) {
    const button = popup.querySelector(
      'button[type="submit"].button.popup__button'
    );
    if (button) {
      if (button.textContent.trim() === "–°–æ—Ö—Ä–∞–Ω–∏—Ç—å")
        button.textContent = "–°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ...";
      else button.textContent = "–°–æ—Ö—Ä–∞–Ω–∏—Ç—å";
    }
  }
}

//* –ü—Ä–µ–¥–Ω–∞—Å—Ç—Ä–æ–π–∫–∞ popup –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
function setupCardViewPopup(card) {
  // –¢—Ä–∞–Ω—Å—Ñ–µ—Ä –¥–∞–Ω–Ω—ã—Ö –∏–∑ –∫–∞—Ä—Ç–æ—á–∫–∏ –≤ popup
  if (card.name && card.link && imageCardViewPopup) {
    // –ö–∞—Ä—Ç–∏–Ω–∫–∞ –∫–∞—Ä—Ç–æ—á–∫–∏
    imageCardViewPopup.src = card.link;
    imageCardViewPopup.alt = card.name;
    // –ù–∞–∑–≤–∞–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏
    if (captionCardViewPopup) captionCardViewPopup.textContent = card.name;
  }
}

//* –ü—Ä–µ–¥–Ω–∞—Å—Ç—Ä–æ–π–∫–∞ popup —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –ø—Ä–æ—Ñ–∏–ª—è
function setupEditProfilePopup() {
  // –¢—Ä–∞–Ω—Å—Ñ–µ—Ä –¥–∞–Ω–Ω—ã—Ö –∏–∑ —Å—Ç—Ä–∞–Ω–∏—Ü—ã –≤ popup
  if (nameProfile && nameProfilePopupInput)
    nameProfilePopupInput.value = nameProfile.textContent; // –∏–º—è –ø—Ä–æ—Ñ–∏–ª—è
  if (descrProfile && descrProfilePopupInput)
    descrProfilePopupInput.value = descrProfile.textContent; // –æ–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ—Ñ–∏–ª—è
}

//* –ü—Ä–µ–¥–Ω–∞—Å—Ç—Ä–æ–π–∫–∞ popup –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –∞–≤–∞—Ç–∞—Ä–∞
function setupAvatarPopup() {
  // –¢—Ä–∞–Ω—Å—Ñ–µ—Ä url –∞–≤–∞—Ç–∞—Ä–∞ –∏–∑ –∫–∞—Ä—Ç–æ—á–∫–∏ –≤ popup
  if (avatarProfile && urlAvatarPopupInput) {
    // –í—ã–¥–µ–ª–µ–Ω–∏–µ –∞–¥—Ä–µ—Å–∞ –∏–∑ backgroundImage
    const urlMatch = avatarProfile.style.backgroundImage.match(
      /url\(["']?([^"')]+)["']?\)/
    );
    const avatarURL = urlMatch ? urlMatch[1] : null;
    // –ü–µ—Ä–µ–¥–∞—á–∞ URL –∞–≤–∞—Ç–∞—Ä–∞ –≤ input —Ñ–æ—Ä–º—ã popup
    if (avatarURL) urlAvatarPopupInput.value = avatarURL;
  }
}

//* –°–±—Ä–æ—Å —Ñ–æ—Ä–º—ã popup —Å–æ–∑–¥–∞–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
function resetNewCardPopupForm() {
  document.forms["new-place"].reset(); // —Å–±—Ä–æ—Å —Ñ–æ—Ä–º—ã (–æ—á–∏—Å—Ç–∫–∞ –ø–æ–ª–µ–π)
}

//* –ü–æ–¥–≥–æ—Ç–æ–≤–∫–∞ –¥–∞–Ω–Ω—ã—Ö —Ñ–æ—Ä–º—ã popup
// –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –æ–±—ä–µ–∫—Ç obj –ø–æ–ª–µ–π (input#name) –∏ –∏—Ö –∑–Ω–∞—á–µ–Ω–∏–π —Ñ–æ—Ä–º—ã form.
// ( obj==={} ‚áí return undefined )
function serializeForm(form) {
  if (form) {
    const { elements } = form;
    const obj = {};
    Array.from(elements)
      .filter((item) => !!item.name)
      .forEach((element) => {
        const { name, value } = element;
        obj[name] = value;
      });
    if (Object.keys(obj).length !== 0) return obj;
  }
}

function handleDeleteCard(cardId) {
  if (document.forms["confirm-del-card"]) {
    // –ü–µ—Ä–µ–¥–∞–µ–º Id –∫–∞—Ä—Ç–æ—á–∫–∏ –≤ popup –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
    document.forms["confirm-del-card"].dataset.cardId = cardId;
    openModal(cardDelConfirmPopup); // –æ—Ç–∫—Ä—ã—Ç–∏–µ popup
  }
}

function handleLikeCard(cardId, like) {
  // –ó–∞–ø—Ä–æ—Å –Ω–∞ –ø–æ—Å—Ç–∞–Ω–æ–≤–∫—É/—É–¥–∞–ª–µ–Ω–∏–µ like (true/false)
  return evaluateCard(cardId, like)
    .then((card) => {
      // –í–æ–≤—Ä–∞—Ç –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ like –∫–∞—Ä—Ç–æ—á–∫–∏
      if (card.likes && Array.isArray(card.likes))
        return Promise.resolve(card.likes.length);
    })
    .catch((err) => {
      return Promise.reject(err);
    });
}

//! –í—ã–≤–æ–¥ –∫–∞—Ä—Ç–æ—á–µ–∫ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É
const appendCards = (cardList, cards) => {
  cards.forEach((card) => {
    const newCard = createCard(card, profile._id, {
      onShow: handleShowCard,
      onDelete: handleDeleteCard,
      onLike: handleLikeCard,
    });
    if (newCard) cardList.append(newCard);
  });
};

//! –í—ã–≤–æ–¥ –¥–∞–Ω–Ω—ã—Ö –ø—Ä–æ—Ñ–∏–ª—è –∏ –µ–≥–æ –∞–≤–∞—Ç–∞—Ä –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É
const renderProfile = ({ name, about, avatar }) => {
  if (nameProfile && name) {
    nameProfile.textContent = name;
  }
  if (descrProfile && about) {
    descrProfile.textContent = about;
  }
  if (avatarProfile && avatar) {
    avatarProfile.style.backgroundImage = `url("${avatar}")`;
  }
};

const initializeApp = () => {
  Promise.all([getProfile(), getCards()])
    .then((resps) => {
      // –û–±—Ä–∞–±–æ—Ç–∫–∞ promise –∑–∞–ø—Ä–æ—Å–∞ –¥–∞–Ω–Ω—ã—Ö –ø—Ä–æ—Ñ–∏–ª—è
      if (resps[0]) {
        profile.data = resps[0]; // –æ–±–Ω–æ–≤–ª–µ–Ω–∏ –ª–æ–∫–∞–ª—å–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö
        renderProfile(resps[0]);
      }
      // –û–±—Ä–∞–±–æ—Ç–∫–∞ promise –∑–∞–ø—Ä–æ—Å–∞ –¥–∞–Ω–Ω—ã—Ö –∫–∞—Ä—Ç–æ—á–µ–∫ –º–µ—Å—Ç
      if (resps[1] && Array.isArray(resps[1]) && resps[1].length > 0) {
        // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–µ–∫, –ø–æ–ª—É—á–µ–Ω–Ω—ã—Ö —Å—Å —Å–µ—Ä–≤–µ—Ä–∞
        appendCards(cardsContainer, resps[1]);
      }
    })
    .catch((err) => {
      console.log(err); // –≤—ã–≤–æ–¥ –æ—à–∏–±–∫b –≤ –∫–æ–Ω—Å–æ–ª—å
    });
};

//! –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è (—Å—Ä–∞–∑—É –ø–æ—Å–ª–µ –∑–∞–≥—Ä—É–∑–∏–ª HTML –∏ –ø–æ—Å—Ç—Ä–æ–µ–Ω–∏—è DOM-–¥–µ—Ä–µ–≤–∞)
document.addEventListener("DOMContentLoaded", () => {
  //* –ü–æ–¥–∫–ª—é—á–µ–Ω–∏–µ –≤–∞–ª–∏–¥–∞—Ü–∏–∏ —Ñ–æ—Ä–º
  enableValidation(SELECTORS);
  initializeApp();
});
