//! –ö–æ–Ω—Å—Ç–∞–Ω—Ç—ã –º–æ–¥—É–ª—è
// CSS-–∫–ª–∞—Å—Å—ã
const CSS_CLASSES = {
  isOpen: "popup_is-opened", // –æ—Ç–∫—Ä—ã—Ç–æ–≥–æ popup
  closer: ".popup__close", // —ç–ª–µ–º–µ–Ω—Ç–∞ (–∫–Ω–æ–ø–∫–∏), –∑–∞–∫—Ä—ã–≤–∞—é—â–µ–≥–æ –æ–∫–Ω–æ
  contentWrapper: ".popup__content", // wrappera –∫–æ–Ω—Ç–µ–Ω—Ç–∞ –æ–∫–Ω–∞
};

//! –û—Ç–∫—Ä—ã—Ç–∏–µ popup
function openModal(popup) {
  if (popup && popup.classList.contains("popup")) {
    // –ù–∞–∑–Ω–∞—á–µ–Ω–∏–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∞ –∑–∞–∫—Ä—ã—Ç–∏—è popup –ø–æ –∫–Ω–æ–ø–∫–µ Esc
    document.addEventListener("keydown", handleEscWrapper);
    popup.classList.add(CSS_CLASSES.isOpen); // –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ popup
  }
}

//! Wrapper –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∞ —Å–æ–±—ã—Ç–∏—è –∑–∞–∫—Ä—ã—Ç–∏—è –æ–∫–Ω–∞ –ø–æ Esc
/*  
  –û–¢–ö–†–´–¢–û–ï –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –º–æ–∂–µ—Ç –±—ã—Ç—å –¢–û–õ–¨–ö–û –û–î–ù–û. –ï—Å–ª–∏ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ 
  –æ—Ç–∫—Ä—ã—Ç—å –Ω–æ–≤–æ–µ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ, —Ç–µ–∫—É—â–µ–µ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ 
  –∑–∞–∫—Ä—ã—Ç—å.
*/
function handleEscWrapper(evt) {
  if (evt.key === "Escape") {
    const popups = document.querySelectorAll(".popup");
    if (popups) {
      //* –ü–æ–∏—Å–∫ –ø–µ—Ä–≤–æ–≥–æ –æ—Ç–∫—Ä—ã—Ç–æ–≥–æ popup
      const openPopup = Array.from(popups).find((popup) => {
        if (popup && popup.classList.contains("popup_is-opened")) return popup;
      });
      //* –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–µ—Ä–≤–æ–≥–æ –Ω–∞–π–¥–µ–Ω–Ω–æ–≥–æ –æ—Ç–∫—Ä—ã—Ç–æ–≥–æ popup
      closeModal(openPopup);
    }
  }
}

//! –ó–∞–∫—Ä—ã—Ç–∏–µ popup
function closeModal(popup) {
  if (popup && popup.classList.contains("popup")) {
    // –£–¥–∞–ª–µ–Ω–∏–µ listner, –∑–∞–∫—Ä—ã–≤–∞—é—â–µ–≥–æ popup –ø–æ Esc
    document.removeEventListener("keydown", handleEscWrapper);
    // C–∫—Ä—ã—Ç–∏–µ popup
    popup.classList.remove(CSS_CLASSES.isOpen);
  }
}

//! –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
const initializeModal = (popup) => {
  if (popup && popup.classList.contains("popup")) {
    //* –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –º–æ–¥–∏—Ñ–∏–∫–∞—Ç–æ—Ä–∞ popup –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏ –ø—Ä–∏ –µ–≥–æ –æ—Ç–∫—Ä—ã—Ç–∏–∏/–∑–∞–∫—Ä—ã—Ç–∏–∏
    if (!popup.classList.contains("popup_is-animated"))
      popup.classList.add("popup_is-animated");

    //* –ù–∞–∑–Ω–∞—á–µ–Ω–∏–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ –∑–∞–∫—Ä—ã—Ç–∏—è popup –ø–æ...
    //... –∫–Ω–æ–ø–∫–µ ‚ùå popup –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ –∫–∞—Ä—Ç–æ—á–∫–∏
    const btnClose = popup.querySelector("button.popup__close");
    if (btnClose)
      btnClose.addEventListener("click", () => {
        closeModal(popup);
      });
    //... click –≤–Ω–µ –≥—Ä–∞–Ω–∏—Ü popup –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ –∫–∞—Ä—Ç–æ—á–∫–∏
    popup.addEventListener("mousedown", (evt) => {
      const isInsideClick = !!evt.target.closest(".popup__content");
      if (!isInsideClick) closeModal(popup);
    });
  }
};

export { openModal, closeModal, initializeModal };
