//! –ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è –∏ –¥–æ—Å—Ç—É–ø
const BASE_URL = "https://nomoreparties.co/v1/wff-cohort-35/"; // –±–∞–∑–æ–≤—ã–π url
const API_TOKEN = process.env.API_TOKEN; // —Ç–æ–∫–µ–Ω

//! –£–Ω–∏—Ñ–∏—Ü–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è –∑–∞–ø—Ä–æ—Å–∞ –∫ —Å–µ—Ä–≤–µ—Ä—É, –≤–æ–∑–≤—Ä–∞—â–∞—é—â–∞—è –µ–≥–æ –æ—Ç–≤–µ—Ç
const httpBaseQuery = ({ url, method = "GET", headers = {}, body = null }) => {
  return fetch(url, {
    method,
    headers,
    ...(body && { body: JSON.stringify(body) }),
  });
};

//! –§—É–Ω–∫—Ü–∏—è –∞–≤—Ç–æ—Ä–∏–∑–∏—Ä–æ–≤–∞–Ω–Ω–æ–≥–æ –∑–∞–ø—Ä–æ—Å–∞ –∫ —Å–µ—Ä–≤–µ—Ä—É, –≤–æ–∑–≤—Ä–∞—â–∞—é—â–∞—è –æ–±—ä–µ–∫—Ç –æ—Ç–≤–µ—Ç–∞
const httpQuery = ({ endURL, method, headers = {}, body }) => {
  headers.authorization = API_TOKEN;
  return httpBaseQuery({ url: BASE_URL + endURL, method, headers, body }).then(
    (resp) => {
      if (resp.ok) return resp.json();
      return Promise.reject(`–û—à–∏–±–∫–∞: ${resp.status}`);
    }
  );
};

//! –ü—Ä–∏–∫–ª–∞–¥–Ω—ã–µ —Ñ—É–Ω–∫—Ü–∏–∏ –∑–∞–ø—Ä–æ—Å–æ–≤

//* –ó–∞–ø—Ä–æ—Å –∫–∞—Ä—Ç–æ—á–µ–∫ –º–µ—Å—Ç (–Ω–µ —Ç–æ–ª—å–∫–æ —Å–≤–æ–∏—Ö)
const getCards = () => {
  return httpQuery({ endURL: "cards" });
};

//* –ó–∞–ø—Ä–æ—Å –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –Ω–æ–≤–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏
const saveCard = (body) => {
  if (body.name && body.link) {
    // –ü—Ä–æ–≤–µ—Ä–∫–∞: —Ç–∏–ø –∫–æ–Ω—Ç–µ–Ω—Ç–∞ body.link - –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ?
    return getHeaders(body.link).then((resp) => {
      if (!resp.ok) return Promise.reject(resp.status);
      const contentType = resp.headers.get("Content-Type"); // —Ç–∏–ø –∫–æ–Ω—Ç–µ–Ω—Ç–∞ –≤ –æ—Ç–≤–µ—Ç–µ
      if (!contentType || !contentType.startsWith("image/"))
        return Promise.reject(`—Å—Å—ã–ª–∫–∞ –Ω–µ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É`);
      // –°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏ –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ
      return httpQuery({
        endURL: "cards",
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body,
      }).then((saveResp) => saveResp);
    });
  }
};

//* –ó–∞–ø—Ä–æ—Å —É–¥–∞–ª–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏ –ø–æ –µ–µ Id
const deleteCard = (cardId) => {
  return httpBaseQuery({
    url: `${BASE_URL}cards/${cardId}`,
    method: "DELETE",
    headers: { authorization: API_TOKEN },
  });
};

//* –ó–∞–ø—Ä–æ—Å –¥–∞–Ω–Ω—ã—Ö —Å–≤–æ–µ–≥–æ –ø—Ä–æ—Ñ–∏–ª—è
const getProfile = () => {
  return httpQuery({ endURL: "users/me" });
};

//* –ò–∑–º–µ–Ω–µ–Ω–∏–µ –ø—Ä–æ—Ñ–∏–ª—è
const setProfile = (body) => {
  return httpQuery({
    endURL: "users/me",
    method: "PATCH",
    headers: {
      "Content-Type": "application/json",
    },
    body,
  });
};

//* –ó–∞–ø—Ä–æ—Å MIME-—Ç–∏–ø–∞ –≤ –∑–∞–≥–æ–ª–æ–≤–∫–µ
const getHeaders = (url) => {
  return httpBaseQuery({
    url,
    method: "HEAD",
    mode: "cors", // –¥–ª—è –∫—Ä–æ—Å—Å-–¥–æ–º–µ–Ω–Ω—ã—Ö –∑–∞–ø—Ä–æ—Å–æ–≤
    cache: "no-cache", // –∏–≥–Ω–æ—Ä–∏—Ä—É–µ–º –∫–µ—à
  });
};

//* –ò–∑–º–µ–Ω–µ–Ω–∏–µ –∞–≤–∞—Ç–∞—Ä–∞ –ø—Ä–æ—Ñ–∏–ª—è
const setProfileAvatar = (body) => {
  return httpQuery({
    endURL: "users/me/avatar",
    method: "PATCH",
    headers: {
      "Content-Type": "application/json",
    },
    body,
  });
};

const evaluateCard = (cardId, like) => {
  return httpQuery({
    endURL: `cards/likes/${cardId}`,
    method: like ? "PUT" : "DELETE",
  });
};

export {
  getCards,
  getProfile,
  setProfile,
  setProfileAvatar,
  getHeaders,
  saveCard,
  deleteCard,
  evaluateCard,
};
